'use client';

import { useEffect, useState } from 'react';
import {
  LineChart,
  Line,
  BarChart,
  Bar,
  PieChart,
  Pie,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from 'recharts';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/app/loan/components/ui/card';
import { Input } from '@/app/loan/components/ui/input';
import { Button } from '@/app/loan/components/ui/button';
import { Label } from '@/app/loan/components/ui/label';
import { Lock, Mail } from 'lucide-react';

interface Application {
  _id: string;
  name: string;
  phone: string;
  loanAmount: string;
  branch: string;
  createdAt: string;
}

interface AdminUser {
  _id: string;
  email: string;
  role?: string;
  createdAt: string;
  isActive: boolean;
}

interface DecodedToken {
  email: string;
  id: string;
  role: string;
  iat?: number;
  exp?: number;
}

export default function AdminPage() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [token, setToken] = useState<string | null>(null);
  const [userRole, setUserRole] = useState<string | null>(null);
  const [applications, setApplications] = useState<Application[]>([]);
  const [adminUsers, setAdminUsers] = useState<AdminUser[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [showCredentialManagement, setShowCredentialManagement] = useState(false);
  const [showCreateForm, setShowCreateForm] = useState(false);
  const [editingUser, setEditingUser] = useState<AdminUser | null>(null);
  const [newUserEmail, setNewUserEmail] = useState('');
  const [newUserPassword, setNewUserPassword] = useState('');
  const [editUserEmail, setEditUserEmail] = useState('');
  const [editUserPassword, setEditUserPassword] = useState('');
  const [editUserActive, setEditUserActive] = useState(true);
  const [filterBranch, setFilterBranch] = useState('');
  const [filterLoanAmount, setFilterLoanAmount] = useState('');
  const [showAnalytics, setShowAnalytics] = useState(false);
  const [visitorAnalytics, setVisitorAnalytics] = useState<any>(null);
  const [loanAnalytics, setLoanAnalytics] = useState<any>(null);
  const [analyticsLoading, setAnalyticsLoading] = useState(false);

  // Helper function to decode JWT token
  const decodeToken = (token: string): DecodedToken | null => {
    try {
      const base64Url = token.split('.')[1];
      const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
      const jsonPayload = decodeURIComponent(
        atob(base64)
          .split('')
          .map((c) => '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2))
          .join(''),
      );
      return JSON.parse(jsonPayload);
    } catch (error) {
      console.error('Error decoding token:', error);
      return null;
    }
  };

  const backendBase =
    process.env.NEXT_PUBLIC_BACKEND_URL || 'http://localhost:4000';

  async function parseJsonSafe(res: Response) {
    const contentType = res.headers.get('content-type') || '';
    if (contentType.includes('application/json')) {
      return res.json();
    }
    const text = await res.text();
    console.error('Expected JSON but got:', text.slice(0, 200));
    throw new Error('Unexpected response from server (not JSON)');
  }

  useEffect(() => {
    const storedToken =
      typeof window !== 'undefined'
        ? window.localStorage.getItem('adminToken')
        : null;
    if (storedToken) {
      setToken(storedToken);
      // Decode token to get user role
      const decoded = decodeToken(storedToken);
      if (decoded && decoded.role) {
        setUserRole(decoded.role);
      }
    }
  }, []);

  useEffect(() => {
    if (!token) return;

    const fetchApplications = async () => {
      try {
        setLoading(true);
        setError(null);

        const res = await fetch(`${backendBase}/api/admin/applications`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        const data = await parseJsonSafe(res);
        if (!res.ok || !data.success) {
          throw new Error(data.error || 'Failed to load applications');
        }

        setApplications(data.applications || []);
      } catch (err) {
        console.error(err);
        setError(
          err instanceof Error ? err.message : 'Failed to load applications',
        );
      } finally {
        setLoading(false);
      }
    };

    fetchApplications();
  }, [token, backendBase]);

  useEffect(() => {
    if (!token || !showCredentialManagement) return;

    const fetchAdminUsers = async () => {
      try {
        const res = await fetch(`${backendBase}/api/admin/users`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        const data = await parseJsonSafe(res);
        if (!res.ok || !data.success) {
          throw new Error(data.error || 'Failed to load admin users');
        }

        setAdminUsers(data.users || []);
      } catch (err) {
        console.error(err);
        setError(
          err instanceof Error ? err.message : 'Failed to load admin users',
        );
      }
    };

    fetchAdminUsers();
  }, [token, backendBase, showCredentialManagement]);

  useEffect(() => {
    if (!token || !showAnalytics || !isSuperAdmin) return;

    const fetchAnalytics = async () => {
      try {
        setAnalyticsLoading(true);
        setError(null);

        const [visitorsRes, loansRes] = await Promise.all([
          fetch(`${backendBase}/api/admin/analytics/visitors?days=30`, {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          fetch(`${backendBase}/api/admin/analytics/loans`, {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
        ]);

        const visitorsData = await parseJsonSafe(visitorsRes);
        const loansData = await parseJsonSafe(loansRes);

        if (visitorsData.success) {
          setVisitorAnalytics(visitorsData.analytics);
        }
        if (loansData.success) {
          setLoanAnalytics(loansData.analytics);
        }
      } catch (err) {
        console.error(err);
        setError(
          err instanceof Error ? err.message : 'Failed to load analytics',
        );
      } finally {
        setAnalyticsLoading(false);
      }
    };

    fetchAnalytics();
  }, [token, backendBase, showAnalytics, userRole]);

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(null);
    setLoading(true);

    try {
      const res = await fetch(`${backendBase}/api/admin/login`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ email, password }),
      });

      const data = await parseJsonSafe(res);
      if (!res.ok || !data.success || !data.token) {
        throw new Error(data.error || 'Invalid email or password');
      }

      setToken(data.token);
      if (typeof window !== 'undefined') {
        window.localStorage.setItem('adminToken', data.token);
        // Decode token to get user role
        const decoded = decodeToken(data.token);
        if (decoded && decoded.role) {
          setUserRole(decoded.role);
        }
      }
    } catch (err) {
      console.error(err);
      setError(err instanceof Error ? err.message : 'Login failed');
    } finally {
      setLoading(false);
    }
  };

  const handleLogout = () => {
    setToken(null);
    setUserRole(null);
    setShowCredentialManagement(false);
    if (typeof window !== 'undefined') {
      window.localStorage.removeItem('adminToken');
    }
  };

  // Check if user is super_admin
  const isSuperAdmin = userRole === 'super_admin';

  // Get unique branches/pincodes for filter dropdown
  const uniqueBranches = Array.from(
    new Set(applications.map((app) => app.branch).filter(Boolean))
  ).sort();

  // Filter applications based on selected filters
  const filteredApplications = applications.filter((app) => {
    const matchesBranch =
      !filterBranch || app.branch.toLowerCase().includes(filterBranch.toLowerCase());
    const matchesLoanAmount =
      !filterLoanAmount ||
      app.loanAmount.toLowerCase().includes(filterLoanAmount.toLowerCase());
    return matchesBranch && matchesLoanAmount;
  });

  const handleClearFilters = () => {
    setFilterBranch('');
    setFilterLoanAmount('');
  };

  const handleCreateUser = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(null);
    setLoading(true);

    try {
      const res = await fetch(`${backendBase}/api/admin/users`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({
          email: newUserEmail,
          password: newUserPassword,
        }),
      });

      const data = await parseJsonSafe(res);
      if (!res.ok || !data.success) {
        throw new Error(data.error || 'Failed to create admin user');
      }

      setNewUserEmail('');
      setNewUserPassword('');
      setShowCreateForm(false);
      
      // Refresh admin users list
      const usersRes = await fetch(`${backendBase}/api/admin/users`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      const usersData = await parseJsonSafe(usersRes);
      if (usersData.success) {
        setAdminUsers(usersData.users || []);
      }
    } catch (err) {
      console.error(err);
      setError(err instanceof Error ? err.message : 'Failed to create admin user');
    } finally {
      setLoading(false);
    }
  };

  const handleEditUser = (user: AdminUser) => {
    setEditingUser(user);
    setEditUserEmail(user.email);
    setEditUserPassword('');
    setEditUserActive(user.isActive);
  };

  const handleUpdateUser = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!editingUser) return;

    setError(null);
    setLoading(true);

    try {
      const updateData: any = {
        email: editUserEmail,
        isActive: editUserActive,
      };
      if (editUserPassword.trim()) {
        updateData.password = editUserPassword;
      }

      const res = await fetch(`${backendBase}/api/admin/users/${editingUser._id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(updateData),
      });

      const data = await parseJsonSafe(res);
      if (!res.ok || !data.success) {
        throw new Error(data.error || 'Failed to update admin user');
      }

      setEditingUser(null);
      setEditUserEmail('');
      setEditUserPassword('');
      
      // Refresh admin users list
      const usersRes = await fetch(`${backendBase}/api/admin/users`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      const usersData = await parseJsonSafe(usersRes);
      if (usersData.success) {
        setAdminUsers(usersData.users || []);
      }
    } catch (err) {
      console.error(err);
      setError(err instanceof Error ? err.message : 'Failed to update admin user');
    } finally {
      setLoading(false);
    }
  };

  const handleDeleteUser = async (userId: string) => {
    if (!confirm('Are you sure you want to delete this admin user?')) return;

    setError(null);
    setLoading(true);

    try {
      const res = await fetch(`${backendBase}/api/admin/users/${userId}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      const data = await parseJsonSafe(res);
      if (!res.ok || !data.success) {
        throw new Error(data.error || 'Failed to delete admin user');
      }

      // Refresh admin users list
      const usersRes = await fetch(`${backendBase}/api/admin/users`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      const usersData = await parseJsonSafe(usersRes);
      if (usersData.success) {
        setAdminUsers(usersData.users || []);
      }
    } catch (err) {
      console.error(err);
      setError(err instanceof Error ? err.message : 'Failed to delete admin user');
    } finally {
      setLoading(false);
    }
  };

  const handleExport = async () => {
    if (!token) return;

    try {
      // Export filtered data if filters are active, otherwise export all
      const dataToExport = filteredApplications.length < applications.length 
        ? filteredApplications 
        : applications;

      // Create CSV content
      const headers = ['Name', 'Phone', 'LoanAmount', 'Branch', 'CreatedAt'];
      const rows = dataToExport.map((app) => [
        `"${app.name || ''}"`,
        `"${app.phone || ''}"`,
        `"${app.loanAmount || ''}"`,
        `"${app.branch || ''}"`,
        `"${app.createdAt || ''}"`,
      ]);

      const csv = [headers.join(','), ...rows.map((r) => r.join(','))].join('\n');

      // Create blob and download
      const blob = new Blob([csv], { type: 'text/csv;charset=utf-8;' });
      const url = window.URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      
      // Generate filename with filter info if applicable
      let filename = 'quick_applications';
      if (filterBranch || filterLoanAmount) {
        const filterParts = [];
        if (filterBranch) filterParts.push(`branch-${filterBranch.replace(/[^a-zA-Z0-9]/g, '_')}`);
        if (filterLoanAmount) filterParts.push(`amount-${filterLoanAmount.replace(/[^a-zA-Z0-9]/g, '_')}`);
        filename = `quick_applications_filtered_${filterParts.join('_')}`;
      }
      a.download = `${filename}.csv`;
      
      document.body.appendChild(a);
      a.click();
      a.remove();
      window.URL.revokeObjectURL(url);
    } catch (err) {
      console.error(err);
      setError(
        err instanceof Error ? err.message : 'Export failed. Please try again.',
      );
    }
  };

  if (!token) {
    return (
      <main className="min-h-screen bg-black text-white flex items-center justify-center">
        <div className="w-full max-w-md px-6 py-8 border border-gray-800 rounded-2xl bg-neutral-950 shadow-lg">
          <h1 className="text-2xl font-semibold mb-6 text-center">
            Admin Login
          </h1>
          <form onSubmit={handleLogin} className="space-y-4">
            <div>
              <label className="block text-sm mb-1">Email</label>
              <input
                type="email"
                className="w-full px-3 py-2 rounded-md bg-neutral-900 border border-gray-700 focus:outline-none focus:ring-2 focus:ring-yellow-500"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </div>
            <div>
              <label className="block text-sm mb-1">Password</label>
              <input
                type="password"
                className="w-full px-3 py-2 rounded-md bg-neutral-900 border border-gray-700 focus:outline-none focus:ring-2 focus:ring-yellow-500"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </div>
            {error && (
              <p className="text-sm text-red-500">
                {error}
              </p>
            )}
            <button
              type="submit"
              disabled={loading}
              className="w-full py-2.5 rounded-md bg-yellow-500 hover:bg-yellow-400 text-black font-medium transition disabled:opacity-60"
            >
              {loading ? 'Signing in...' : 'Sign In'}
            </button>
          </form>
        </div>
      </main>
    );
  }

  return (
    <main className="min-h-screen bg-black text-white px-4 py-8">
      <div className="max-w-6xl mx-auto">
        <div className="flex items-center justify-between mb-6">
          <h1 className="text-3xl font-semibold">Quick Applications</h1>
          <div className="flex items-center gap-3">
            {isSuperAdmin && (
              <>
                <button
                  onClick={() => setShowAnalytics(!showAnalytics)}
                  className="px-4 py-2 rounded-md bg-purple-600 hover:bg-purple-700 text-white font-medium transition"
                >
                  {showAnalytics ? 'Hide' : 'Show'} Analytics
                </button>
                <button
                  onClick={() => setShowCredentialManagement(!showCredentialManagement)}
                  className="px-4 py-2 rounded-md bg-gray-700 hover:bg-gray-600 text-white font-medium transition"
                >
                  {showCredentialManagement ? 'Hide' : 'Manage'} Credentials
                </button>
              </>
            )}
            <button
              onClick={handleExport}
              className="px-4 py-2 rounded-md bg-yellow-500 hover:bg-yellow-400 text-black font-medium transition text-sm"
              title={filterBranch || filterLoanAmount ? 'Export filtered data' : 'Export all data'}
            >
              Export CSV {filterBranch || filterLoanAmount ? '(Filtered)' : ''}
            </button>
            <button
              onClick={handleLogout}
              className="px-4 py-2 rounded-md bg-red-600 hover:bg-red-700 text-white font-medium transition"
            >
              Logout
            </button>
          </div>
        </div>

        {/* Analytics Dashboard - Only for Super Admin */}
        {showAnalytics && isSuperAdmin && (
          <div className="mb-6 p-5 border border-gray-800 rounded-lg bg-gradient-to-br from-neutral-950 to-neutral-900">
            <div className="flex items-center justify-between mb-5">
              <h2 className="text-xl font-bold text-white">Analytics Dashboard</h2>
              <span className="text-xs text-gray-400">Real-time Insights</span>
            </div>

            {analyticsLoading ? (
              <div className="flex items-center justify-center py-8">
                <div className="text-sm text-gray-400">Loading analytics...</div>
              </div>
            ) : (
              <div className="space-y-4">
                {/* Key Metrics - Compact Cards */}
                <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
                  {loanAnalytics && (
                    <>
                      <div className="p-3 bg-gradient-to-br from-yellow-500/10 to-yellow-600/5 border border-yellow-500/20 rounded-lg">
                        <div className="text-xs text-gray-400 mb-1">Total Applications</div>
                        <div className="text-lg font-bold text-yellow-400">
                          {loanAnalytics.totalApplications}
                        </div>
                      </div>
                      <div className="p-3 bg-gradient-to-br from-green-500/10 to-green-600/5 border border-green-500/20 rounded-lg">
                        <div className="text-xs text-gray-400 mb-1">Last 7 Days</div>
                        <div className="text-lg font-bold text-green-400">
                          {loanAnalytics.recentApplications}
                        </div>
                      </div>
                      <div className="p-3 bg-gradient-to-br from-blue-500/10 to-blue-600/5 border border-blue-500/20 rounded-lg">
                        <div className="text-xs text-gray-400 mb-1">Total Amount</div>
                        <div className="text-sm font-bold text-blue-400">
                          â‚¹{(loanAnalytics.totalLoanAmount / 100000).toFixed(1)}L
                        </div>
                      </div>
                    </>
                  )}
                  {visitorAnalytics && (
                    <div className="p-3 bg-gradient-to-br from-purple-500/10 to-purple-600/5 border border-purple-500/20 rounded-lg">
                      <div className="text-xs text-gray-400 mb-1">Total Visitors</div>
                      <div className="text-lg font-bold text-purple-400">
                        {visitorAnalytics.totalVisitors}
                      </div>
                    </div>
                  )}
                </div>

                {/* Charts Grid - Compact */}
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-4">
                  {/* Daily Registrations Chart */}
                  {loanAnalytics?.applicationsByDate && loanAnalytics.applicationsByDate.length > 0 && (
                    <div className="p-4 bg-neutral-900/50 border border-gray-800 rounded-lg">
                      <h4 className="text-sm font-semibold mb-3 text-gray-300">Daily Registrations</h4>
                      <div className="h-48">
                        <ResponsiveContainer width="100%" height="100%">
                          <LineChart
                            data={loanAnalytics.applicationsByDate.slice(-14).map((item: any) => ({
                              date: new Date(item._id).toLocaleDateString('en-IN', { month: 'short', day: 'numeric' }),
                              count: item.count,
                            }))}
                            margin={{ top: 5, right: 10, left: -10, bottom: 5 }}
                          >
                            <CartesianGrid strokeDasharray="3 3" stroke="#374151" opacity={0.3} />
                            <XAxis dataKey="date" stroke="#9ca3af" style={{ fontSize: '10px' }} />
                            <YAxis stroke="#9ca3af" style={{ fontSize: '10px' }} />
                            <Tooltip
                              contentStyle={{
                                backgroundColor: '#1f2937',
                                border: '1px solid #374151',
                                borderRadius: '6px',
                                fontSize: '12px',
                              }}
                            />
                            <Line
                              type="monotone"
                              dataKey="count"
                              stroke="#fbbf24"
                              strokeWidth={2}
                              dot={false}
                            />
                          </LineChart>
                        </ResponsiveContainer>
                      </div>
                    </div>
                  )}

                  {/* Applications by Pincode - Bar Chart */}
                  {loanAnalytics?.applicationsByBranch && loanAnalytics.applicationsByBranch.length > 0 && (
                    <div className="p-4 bg-neutral-900/50 border border-gray-800 rounded-lg">
                      <h4 className="text-sm font-semibold mb-3 text-gray-300">Top Pincodes</h4>
                      <div className="h-48">
                        <ResponsiveContainer width="100%" height="100%">
                          <BarChart
                            data={loanAnalytics.applicationsByBranch.slice(0, 6).map((item: any) => ({
                              name: item._id || 'Unknown',
                              count: item.count,
                            }))}
                            margin={{ top: 5, right: 10, left: -10, bottom: 40 }}
                          >
                            <CartesianGrid strokeDasharray="3 3" stroke="#374151" opacity={0.3} />
                            <XAxis
                              dataKey="name"
                              stroke="#9ca3af"
                              angle={-45}
                              textAnchor="end"
                              height={60}
                              style={{ fontSize: '10px' }}
                            />
                            <YAxis stroke="#9ca3af" style={{ fontSize: '10px' }} />
                            <Tooltip
                              contentStyle={{
                                backgroundColor: '#1f2937',
                                border: '1px solid #374151',
                                borderRadius: '6px',
                                fontSize: '12px',
                              }}
                            />
                            <Bar dataKey="count" fill="#fbbf24" radius={[3, 3, 0, 0]} />
                          </BarChart>
                        </ResponsiveContainer>
                      </div>
                    </div>
                  )}
                </div>

                {/* Website Visitors Section - Compact */}
                {visitorAnalytics && (
                  <div className="p-4 bg-neutral-900/50 border border-gray-800 rounded-lg">
                    <h3 className="text-sm font-semibold mb-3 text-gray-300">Website Traffic</h3>
                    <div className="grid grid-cols-3 gap-3 mb-3">
                      <div className="p-2 bg-neutral-800/50 rounded text-center">
                        <div className="text-xs text-gray-400 mb-1">Total</div>
                        <div className="text-base font-bold text-yellow-400">
                          {visitorAnalytics.totalVisitors}
                        </div>
                      </div>
                      <div className="p-2 bg-neutral-800/50 rounded text-center">
                        <div className="text-xs text-gray-400 mb-1">Unique</div>
                        <div className="text-base font-bold text-blue-400">
                          {visitorAnalytics.uniqueVisitors}
                        </div>
                      </div>
                      <div className="p-2 bg-neutral-800/50 rounded text-center">
                        <div className="text-xs text-gray-400 mb-1">Return</div>
                        <div className="text-base font-bold text-green-400">
                          {visitorAnalytics.totalVisitors > 0
                            ? Math.round(
                                ((visitorAnalytics.totalVisitors - visitorAnalytics.uniqueVisitors) /
                                  visitorAnalytics.totalVisitors) * 100
                              )
                            : 0}%
                        </div>
                      </div>
                    </div>
                  </div>
                )}
              </div>
            )}
          </div>
        )}

        {showCredentialManagement && isSuperAdmin && (
          <div className="mb-8 p-6 border border-gray-800 rounded-xl bg-neutral-950">
            <div className="flex items-center justify-between mb-4">
              <h2 className="text-2xl font-semibold">Admin Credential Management</h2>
              {!showCreateForm && !editingUser && (
                <button
                  onClick={() => setShowCreateForm(true)}
                  className="px-4 py-2 rounded-md bg-green-600 hover:bg-green-700 text-white font-medium transition"
                >
                  Create New Admin
                </button>
              )}
            </div>

            {showCreateForm && !editingUser && (
              <form onSubmit={handleCreateUser} className="mb-6 p-4 border border-gray-700 rounded-lg bg-neutral-900">
                <h3 className="text-lg font-semibold mb-4">Create New Admin User</h3>
                <div className="space-y-3">
                  <div>
                    <label className="block text-sm mb-1">Email</label>
                    <input
                      type="email"
                      className="w-full px-3 py-2 rounded-md bg-neutral-800 border border-gray-600 focus:outline-none focus:ring-2 focus:ring-yellow-500"
                      value={newUserEmail}
                      onChange={(e) => setNewUserEmail(e.target.value)}
                      required
                    />
                  </div>
                  <div>
                    <label className="block text-sm mb-1">Password</label>
                    <input
                      type="password"
                      className="w-full px-3 py-2 rounded-md bg-neutral-800 border border-gray-600 focus:outline-none focus:ring-2 focus:ring-yellow-500"
                      value={newUserPassword}
                      onChange={(e) => setNewUserPassword(e.target.value)}
                      required
                      minLength={6}
                    />
                    <p className="text-xs text-gray-400 mt-1">Password must be at least 6 characters</p>
                  </div>
                  <div className="flex gap-2">
                    <button
                      type="submit"
                      disabled={loading}
                      className="px-4 py-2 rounded-md bg-green-600 hover:bg-green-700 text-white font-medium transition disabled:opacity-60"
                    >
                      {loading ? 'Creating...' : 'Create'}
                    </button>
                    <button
                      type="button"
                      onClick={() => {
                        setShowCreateForm(false);
                        setNewUserEmail('');
                        setNewUserPassword('');
                      }}
                      className="px-4 py-2 rounded-md bg-gray-600 hover:bg-gray-700 text-white font-medium transition"
                    >
                      Cancel
                    </button>
                  </div>
                </div>
              </form>
            )}

            {editingUser && (() => {
              const isSuperAdminUser = editingUser.email === 'admin@yellowmetal.com' || editingUser.role === 'super_admin';
              return (
                <form onSubmit={handleUpdateUser} className="mb-6 p-4 border border-gray-700 rounded-lg bg-neutral-900">
                  <h3 className="text-lg font-semibold mb-4">
                    Edit Admin User
                    {isSuperAdminUser && (
                      <span className="ml-2 text-sm text-yellow-400">(Super Admin - Limited Editing)</span>
                    )}
                  </h3>
                  <div className="space-y-3">
                    <div>
                      <label className="block text-sm mb-1">Email</label>
                      <input
                        type="email"
                        className={`w-full px-3 py-2 rounded-md bg-neutral-800 border border-gray-600 focus:outline-none focus:ring-2 focus:ring-yellow-500 ${
                          isSuperAdminUser ? 'opacity-60 cursor-not-allowed' : ''
                        }`}
                        value={editUserEmail}
                        onChange={(e) => setEditUserEmail(e.target.value)}
                        disabled={isSuperAdminUser}
                        required
                      />
                      {isSuperAdminUser && (
                        <p className="text-xs text-gray-400 mt-1">Super admin email cannot be changed</p>
                      )}
                    </div>
                    <div>
                      <label className="block text-sm mb-1">New Password (leave blank to keep current)</label>
                      <input
                        type="password"
                        className="w-full px-3 py-2 rounded-md bg-neutral-800 border border-gray-600 focus:outline-none focus:ring-2 focus:ring-yellow-500"
                        value={editUserPassword}
                        onChange={(e) => setEditUserPassword(e.target.value)}
                        minLength={6}
                      />
                      <p className="text-xs text-gray-400 mt-1">Leave blank to keep current password</p>
                    </div>
                    <div>
                      <label className="flex items-center gap-2">
                        <input
                          type="checkbox"
                          checked={editUserActive}
                          onChange={(e) => setEditUserActive(e.target.checked)}
                          disabled={isSuperAdminUser}
                          className="rounded"
                        />
                        <span className={`text-sm ${isSuperAdminUser ? 'text-gray-500' : ''}`}>
                          Active
                          {isSuperAdminUser && ' (Cannot deactivate super admin)'}
                        </span>
                      </label>
                    </div>
                    <div className="flex gap-2">
                      <button
                        type="submit"
                        disabled={loading}
                        className="px-4 py-2 rounded-md bg-blue-600 hover:bg-blue-700 text-white font-medium transition disabled:opacity-60"
                      >
                        {loading ? 'Updating...' : 'Update'}
                      </button>
                      <button
                        type="button"
                        onClick={() => {
                          setEditingUser(null);
                          setEditUserEmail('');
                          setEditUserPassword('');
                        }}
                        className="px-4 py-2 rounded-md bg-gray-600 hover:bg-gray-700 text-white font-medium transition"
                      >
                        Cancel
                      </button>
                    </div>
                  </div>
                </form>
              );
            })()}

            <div className="overflow-x-auto border border-gray-800 rounded-xl">
              <table className="min-w-full text-sm">
                <thead className="bg-neutral-900">
                  <tr>
                    <th className="px-4 py-2 text-left">Email</th>
                    <th className="px-4 py-2 text-left">Role</th>
                    <th className="px-4 py-2 text-left">Created At</th>
                    <th className="px-4 py-2 text-left">Status</th>
                    <th className="px-4 py-2 text-left">Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {adminUsers.map((user) => {
                    const isSuperAdmin = user.email === 'admin@yellowmetal.com' || user.role === 'super_admin';
                    return (
                      <tr
                        key={user._id}
                        className="border-t border-neutral-800 hover:bg-neutral-900/60"
                      >
                        <td className="px-4 py-2">
                          {user.email}
                          {isSuperAdmin && (
                            <span className="ml-2 text-xs text-yellow-400">(Super Admin)</span>
                          )}
                        </td>
                        <td className="px-4 py-2">
                          <span className={`px-2 py-1 rounded text-xs ${
                            isSuperAdmin 
                              ? 'bg-yellow-900 text-yellow-300' 
                              : 'bg-blue-900 text-blue-300'
                          }`}>
                            {isSuperAdmin ? 'Super Admin' : 'Admin'}
                          </span>
                        </td>
                        <td className="px-4 py-2">
                          {new Date(user.createdAt).toLocaleString('en-IN')}
                        </td>
                        <td className="px-4 py-2">
                          <span className={`px-2 py-1 rounded text-xs ${
                            user.isActive ? 'bg-green-900 text-green-300' : 'bg-red-900 text-red-300'
                          }`}>
                            {user.isActive ? 'Active' : 'Inactive'}
                          </span>
                        </td>
                        <td className="px-4 py-2">
                          <div className="flex gap-2">
                            {isSuperAdmin ? (
                              <span className="text-xs text-gray-500">Protected</span>
                            ) : (
                              <>
                                <button
                                  onClick={() => handleEditUser(user)}
                                  className="px-2 py-1 rounded bg-blue-600 hover:bg-blue-700 text-white text-xs transition"
                                >
                                  Edit
                                </button>
                                <button
                                  onClick={() => handleDeleteUser(user._id)}
                                  className="px-2 py-1 rounded bg-red-600 hover:bg-red-700 text-white text-xs transition"
                                >
                                  Delete
                                </button>
                              </>
                            )}
                          </div>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          </div>
        )}

        {error && (
          <p className="text-sm text-red-500 mb-4">
            {error}
          </p>
        )}

        {/* Filter Section */}
        <div className="mb-6 p-4 border border-gray-800 rounded-xl bg-neutral-950">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-xl font-semibold">Filter Applications</h2>
            {(filterBranch || filterLoanAmount) && (
              <button
                onClick={handleClearFilters}
                className="px-3 py-1.5 rounded-md bg-gray-700 hover:bg-gray-600 text-white text-sm font-medium transition"
              >
                Clear Filters
              </button>
            )}
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm mb-2">Branch / Pincode</label>
              <select
                value={filterBranch}
                onChange={(e) => setFilterBranch(e.target.value)}
                className="w-full px-3 py-2 rounded-md bg-neutral-900 border border-gray-700 focus:outline-none focus:ring-2 focus:ring-yellow-500 text-white"
              >
                <option value="">All Branches / Pincodes</option>
                {uniqueBranches.map((branch) => (
                  <option key={branch} value={branch}>
                    {branch}
                  </option>
                ))}
              </select>
            </div>
            <div>
              <label className="block text-sm mb-2">Loan Amount</label>
              <input
                type="text"
                placeholder="Enter loan amount to filter..."
                value={filterLoanAmount}
                onChange={(e) => setFilterLoanAmount(e.target.value)}
                className="w-full px-3 py-2 rounded-md bg-neutral-900 border border-gray-700 focus:outline-none focus:ring-2 focus:ring-yellow-500 text-white placeholder-gray-500"
              />
            </div>
          </div>
          {(filterBranch || filterLoanAmount) && (
            <div className="mt-4 text-sm text-gray-400">
              Showing {filteredApplications.length} of {applications.length} applications
            </div>
          )}
        </div>

        {loading ? (
          <p className="text-sm text-gray-400">Loading applications...</p>
        ) : filteredApplications.length === 0 ? (
          <p className="text-sm text-gray-400">
            {applications.length === 0
              ? 'No applications found.'
              : 'No applications match the selected filters.'}
          </p>
        ) : (
          <div className="overflow-x-auto border border-gray-800 rounded-xl">
            <table className="min-w-full text-sm">
              <thead className="bg-neutral-900">
                <tr>
                  <th className="px-4 py-2 text-left">Name</th>
                  <th className="px-4 py-2 text-left">Phone</th>
                  <th className="px-4 py-2 text-left">Loan Amount</th>
                  <th className="px-4 py-2 text-left">Branch / Pincode</th>
                  <th className="px-4 py-2 text-left">Created At</th>
                </tr>
              </thead>
              <tbody>
                {filteredApplications.map((app) => (
                  <tr
                    key={app._id}
                    className="border-t border-neutral-800 hover:bg-neutral-900/60"
                  >
                    <td className="px-4 py-2">{app.name}</td>
                    <td className="px-4 py-2">{app.phone}</td>
                    <td className="px-4 py-2">{app.loanAmount}</td>
                    <td className="px-4 py-2">{app.branch}</td>
                    <td className="px-4 py-2">
                      {new Date(app.createdAt).toLocaleString('en-IN')}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </main>
  );
}


